/* @import-normalize; bring in normalize.css styles */

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ecf0f1;
  font: normal 16px Manrope, sans-serif;
}
body::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  box-sizing: border-box;
}
img,
ul,
li,
a,
p,
h1,
h2,
h3,
h4,
h5,
article,
ol {
  margin: 0;
  padding: 0;
}
img {
  display: block;
}
ul,
li,
ol {
  list-style: none;
}
a {
  color: inherit;
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}
::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-track {
  background: rgb(37, 37, 37);
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  background: white;
}

.button-primary {
  color: white;
  background-color: #3470ff;
  text-align: center;
  font-size: 14px;
  line-height: 20px;
  font-weight: 600;
  padding: 12px;
  transition: all 0.1s ease-in-out;
  border-radius: 12px;
  outline: none;
  border: none;
  width: 100%;
  cursor: pointer;
}
.button-primary:hover {
  background-color: #0b44cd;
}
.btn-rent {
  display: block;
  width: 168px;
  height: 44px;
  margin-top: 24px;
}
.btn-search {
  display: block;
  width: 136px;

  padding: 14px 44px;
}

.button-secondary {
  color: #3470ff;
  text-decoration: underline;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  transition: all 0.1s ease-in-out;
  border: none;
  cursor: pointer;
  text-underline-offset: 2px;
  background-color: transparent;
}
.button-secondary:hover {
  color: #0b44cd;
  transform: scale(1.05);
}

.btn-home {
  display: block;
  width: 200px;
  position: relative;
  /* z-index: 10; */
}
